'use client'

import Image from 'next/image'
import { useState } from 'react'

interface BlogImagesProps {
  imageUrls: string[] // ·∫£nh + video URLs
  blogId?: string
}

export default function BlogImages({ imageUrls }: BlogImagesProps) {
  const [isOpen, setIsOpen] = useState(false)
  const [currentIndex, setCurrentIndex] = useState(0)

  if (!imageUrls || imageUrls.length === 0) return null

  const openLightbox = (index: number) => {
    setCurrentIndex(index)
    setIsOpen(true)
  }

  const nextIndex = () => (currentIndex + 1) % imageUrls.length
  const prevIndex = () =>
    (currentIndex - 1 + imageUrls.length) % imageUrls.length

  const displayUrls = imageUrls.slice(0, 4)

  const isVideo = (url: string) =>
    url.endsWith('.mp4') || url.endsWith('.mov') || url.endsWith('.webm')

  return (
    <div className="w-full aspect-square grid gap-1 overflow-hidden rounded-lg">
      {/* Grid */}
      {displayUrls.length === 1 && (
        <div
          className="relative w-full h-full cursor-pointer"
          onClick={() => openLightbox(0)}
        >
          {isVideo(displayUrls[0]) ? (
            <div className="w-full h-full relative">
              <video src={displayUrls[0]} className="object-cover w-full h-full" muted />
              <div className="absolute inset-0 flex items-center justify-center text-white text-4xl">
                ‚ñ∂
              </div>
            </div>
          ) : (
            <Image src={displayUrls[0]} alt="blog image" fill className="object-cover" />
          )}
        </div>
      )}

      {displayUrls.length > 1 && (
        <div
          className={`grid ${
            displayUrls.length === 2 ? 'grid-cols-2' : 'grid-cols-2 grid-rows-2'
          } gap-1 h-full`}
        >
          {displayUrls.map((url, idx) => (
            <div
              key={idx}
              className="relative cursor-pointer"
              onClick={() => openLightbox(idx)}
            >
              {isVideo(url) ? (
                <div className="w-full h-full relative">
                  <video src={url} className="object-cover w-full h-full" muted />
                  <div className="absolute inset-0 flex items-center justify-center text-white text-4xl">
                    ‚ñ∂
                  </div>
                </div>
              ) : (
                <Image src={url} alt={`blog image ${idx}`} fill className="object-cover" />
              )}
              {idx === 3 && imageUrls.length > 4 && (
                <div className="absolute inset-0 bg-black/50 flex items-center justify-center text-white text-2xl font-bold">
                  +{imageUrls.length - 4}
                </div>
              )}
            </div>
          ))}
        </div>
      )}

      {/* Lightbox */}
      {isOpen && (
        <div
          className="fixed inset-0 z-50 flex items-center justify-center bg-black/80"
          onClick={() => setIsOpen(false)}
        >
          <button
            className="absolute left-4 text-white text-3xl"
            onClick={(e) => {
              e.stopPropagation()
              setCurrentIndex(prevIndex())
            }}
          >
            ‚Äπ
          </button>

          <div className="relative w-4/5 max-w-3xl h-[80%]">
            {isVideo(imageUrls[currentIndex]) ? (
              <video
                src={imageUrls[currentIndex]}
                className="w-full h-full object-contain"
                controls
                autoPlay
              />
            ) : (
              <Image
                src={imageUrls[currentIndex]}
                alt={`lightbox image ${currentIndex}`}
                fill
                className="object-contain"
              />
            )}
          </div>

          <button
            className="absolute right-4 text-white text-3xl"
            onClick={(e) => {
              e.stopPropagation()
              setCurrentIndex(nextIndex())
            }}
          >
            ‚Ä∫
          </button>
        </div>
      )}
    </div>
  )
}
